.fp-container {
    max-width: 1600px;
    margin: 100px auto 100px auto;
    width: 98%;
}

.fp-h1-container {
    margin-bottom: 5px;
}

.fp {
    margin: 0;
    font-family: 'Lato';
    font-size: 17px;
    color: #67182e;
    letter-spacing: 2px;
    text-align: center;
    margin-left: -1px;
}

.nav_separator-fp {
    background-color: black;
    height: 1px;
}

.fp-info1-container {
    display: flex;
    margin: 0;  
    justify-content: space-between; 
    width: 100%;
    flex-direction: column;
}

.fp-info1-title-container {
    display: flex;
    flex-direction: column;
}

.fp-info1-title {
    font-family: 'Lato';
    color: #67182e;
    font-weight: 900;
    margin-bottom: 0px;
    text-align: center;
}
.fp-info1-year {
    margin-top: 0px;
    text-align: center;
}

#fp-info1-roles-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    width: 278px;
    margin: 0 auto 0 auto;
}

.fp-info1-info {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 278px;
    margin: 0 auto;
}

.fp-info1-info-p {
    font-family: 'Lato';
    line-height: 20px;
    font-size: 14px;
    margin-top: 10px;
    text-align: center;
}

#mbs-info-span {
    display: none;
}

.fp-info1-info-p-span{
    color: #67182e;
    font-weight: 700;
    cursor: pointer;
}

.fp-info1-info-roles {
    margin-top: 10px;
    font-family: 'Lato';
    line-height: 22px;
}

.fp-showcase-container {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    margin-top: 10px;
}

.fp-showcase-1 {
    background-size:100% 100%;
    width: 278px;
    height: 156.38px;
    display: flex;
    align-items: center;
    justify-content: center;    
}

.fp-showcase-2-mbs {
    display: none;
    background-color: white;
    width: 49%;
    height: 100%;
    @media (min-width:900px){
        display: none;
        background-image: url(../../assets/mbs2.png);
        background-size:100% 100%;
        width:400px;
        height: 300px;
    }
    @media (min-width:1000px){
        display:flex;
        width:500px;
        height:281px;
    }
}

.fp-showcase-1-algo {
    background-image: url(../../assets/algorythmia-png.png);
    background-size:100% 100%;
    width: 49%;
    height: 281px;
    display: flex;
    align-items: center;
    justify-content: center;    
}

.fp-showcase-2-algo {
    background-color: white;
    width: 49%;
    height: 281px;
    @media (min-width:900px){
        display: none;
        background-image: url(../../assets/algorythmia2.png);
        background-size:100% 100%;
        width:400px;
        height: 300px;
    }
    @media (min-width:1000px){
        display:flex;
        width:500px;
        height:281px;
    }
}

.fp-showcase-button {
    background-color: #67182e;
    width: 169px;
    height: 30px;
    border-radius: .25rem;
    color: white;
    font-family: 'Lato';
    opacity: 1;
    font-size: 18px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fp-showcase-button:hover {
    color: black;
    transition: .3s;
}

.fp-info2-title {
    color: #67182e;
    text-align: center;
    margin-bottom: 0px;
}

.fp-info2-year {
    margin-top: 0px;
    text-align: center;
}

.fp-info2-info-p {
    text-align: center;
}

#fp-info2-roles-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    width: 265px;
    margin: 0 auto;
}

.fp-showcase-2 {
    display: none;
    background-size: 100% 100%;
    width: 330px;
    height: 185.63;
}

#fp-showcase-mbs-1 {
    background-image: url(../../assets/mbs-png.png);
}

#fp-showcase-algo-1 {
    background-image: url(../../assets/algorythmia-png.png);
}

#fp-showcase-mbs-2 {
    background-image: url(../../assets/mbs2.png);
}

#fp-showcase-algo-2 {
    background-image: url(../../assets/algorythmia2.png);
}

