        width: 50%;
        text-align: left;
    }

    #fp-info2-roles-container {
        width: 50%;
        font-size: 12px;
    }

    .fp-info2-info-roles {
        font-size: 12px;
        line-height: 20px;
        font-family: 'Lato';
        margin-top: 10px;
    }

    .fp-container {
        width: 690px;
    }
